#heading {
  text-align: center;
  font-size: 20px;
}

.courses {
  display: inline-block;
}

.subject {
  height: 175px;
  width: 350px;
  border: 1px solid; 
  border-radius: 2%;
  margin: 15px; 
  
}

.subject p {
  text-decoration: none;
  color: white;
  font-size: 40px;
  text-align: center;
}

#sub1 {
  background: $blue;
  border-color: darken($blue, 10%);
  float: left;

}

